.wp-block-ti-blocks-threeimporter {
	background-color: transparent;
}

.three-importer-container {
	width: 100%;
    height: 100%; 
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    padding: 0;
    margin: 0;
}

.three-importer-container canvas {
    position: absolute;
    top: 0;
    left: 0;
    width: 100% !important;
    height: 100% !important;
    display: block;
    // z-index: 0;
}

.three-importer-container .ti-content {
    z-index: 1;
    position: relative;
}

.three-importer-container.alignfull {
  width: 100vw;     
  margin-left: calc(50% - 50vw);  
  margin-right: calc(50% - 50vw);
  max-width: 100vw;     
  position: relative;    
}